import {
    Component,
    h,
    Prop,
    Element,
    Event,
    EventEmitter,
    State,
} from '@stencil/core';
import { cva } from 'class-variance-authority';
import { AtuiEvent } from '../../../types/events';

const variants = cva(
    'focus-visible:border-active-foreground focus-visible:ring-active-foreground/40 relative flex h-[30px] items-center gap-4 rounded-md border border-solid border-transparent px-12 py-8 leading-none font-normal transition-colors duration-300 ease-in-out outline-none focus-visible:ring-2 focus-visible:ring-offset-0',
    {
        variants: {
            active: {
                true: 'bg-active-foreground/15 text-active',
                false: 'text-dark',
            },
            disabled: {
                true: 'text-disabled hover:text-disabled pointer-events-none',
                false: null,
            },
            hostDisabled: {
                true: 'text-disabled hover:text-disabled pointer-events-none',
                false: null,
            },
        },
    },
);

/**
 * @category Form Controls
 * @description A button group option component for the button group.
 */

@Component({
    tag: 'atui-button-group-option',
    shadow: false,
})
export class AtuiButtonGroupOption {
    /**
     * ID of the button element and its option
     */
    @Prop()
    option_id?: string;

    /**
     * String to be displayed in the button
     */
    @Prop()
    label: string;

    /**
     * Icon to be displayed in the button
     */
    @Prop()
    icon: string;

    /**
     * Will disable interaction if set
     */
    @Prop()
    disabled: boolean;

    /**
     * Will apply active styling to the button
     */
    @Prop()
    is_active: boolean;

    @Element() el;

    /**
     * Emits when the button is clicked
     */
    @Event()
    atuiClick: EventEmitter<AtuiEvent>;

    @State()
    host_disabled: boolean;

    private provider: HTMLAtuiButtonGroupElement;

    async componentDidLoad() {
        this.provider = this.el.closest(
            'atui-button-group',
        ) as HTMLAtuiButtonGroupElement;
        this.host_disabled = this.provider.disabled;
    }

    handleClick(event: Event) {
        this.is_active = true;
        this.atuiClick.emit({
            originalEvent: event,
            componentType: 'atui-button-group-option',
            element: this.el,
        });
    }

    render() {
        const classname = variants({
            active: this.is_active,
            disabled: this.disabled,
            hostDisabled: this.host_disabled,
        });

        return (
            <button
                class={classname}
                role="radio"
                tabindex={0}
                aria-checked={this.is_active}
                disabled={this.disabled}
                onClick={(event) => this.handleClick(event)}
                data-name="button-group-option"
                type="button"
            >
                <slot name="icon" />
                {this.label}
            </button>
        );
    }
}
